<template>
  <div class="invite">
    <div class="code">
      <h1>我是{{data.nickname}}</h1>
      <h1>我为您推荐公众号</h1>
      <img src />
      <p>长按识别二维码</p>
      <p>关注公众号</p>
      <img class="codeImg" :src="data.xcx_qrcode" />
    </div>
    <div class="bot">
      <div class="left flex">
        <img src="../../assets/images/invitation_dload@2x.png" />
        <p>保存到相册</p>
      </div>
      <div class="right flex">
        <img src="../../assets/images/invitation_share@2x.png" />
        <p>分享给好友</p>
      </div>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      data: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.share, params).then(res => {
        this.data = res.data.data;
      });
    }
  }
};
</script>


<style scoped lang="scss">
.invite {
  background: url(../../assets/images/invitation_bg@2x.png) no-repeat;
  background-size: 100% 100%;
  height: 100%;
  width: 100%;
  padding-top: 150px;
  .code {
    width: 313px;
    height: 353px;
    margin: 0 auto;
    background: rgba(255, 255, 255, 1);
    border-radius: 13px;
    text-align: center;
    padding-top: 20px;
    h1 {
      font-size: 18px;
    }
    .codeImg {
      height: 174px;
      width: 174px;
    }
  }
  .bot {
    display: flex;
    justify-content: space-around;
    color: #fff;
    margin-top: 30px;
    .flex {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    img {
      width: 30px;
      height: 30px;
      margin-bottom: 10px;
    }
  }
}
</style>



